var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"article"},[_c('div',{staticClass:"about-us-top-img"}),_vm._v(" "),_c('div',{staticClass:"title"},[_vm._v("\n    浅谈园林景观置石、叠石设计\n  ")]),_vm._v(" "),_c('p',{staticClass:"content"},[_vm._v("\n    　在施工图设计阶段如何表示园林中叠石、置石？如何设计石景？尤其设计自然溪流中的置石，自然式山石驳岸、假山、山石安放等各项，不知如何画出来？\n\n\n    　　这确实是目前许多景观设计公司的薄弱点，甚至盲点，搞景观的人不会设计石景是很遗憾的。\n\n\n    一、现在\n\n\n    　　目前，设计自然式目林景石时，方案设计阶段山石表现可用电脑技术，采月其它已建作品给业主看，十分有效，顺利通过方案，然而一进入施工图阶段，设计人员被难住了，设计人员画不出各种各样构思中的石景!人工不会画，电脑是根据人脑画的，当然也不会画，无从表示之，有的干脆不画，造成山石单项施工图空白。工程在工地上缺少了山石施工图指导只能由施工人员随便、随意堆叠，若遇上技术较差的工程队伍叠出的作品会令人啼笑皆非，无法掌控。\n\n\n    　　的确，千变万化山石的施工图很难表示。设计人员需用工程语言按比例绘图出设计作品的形状、石型、质感、高度、厚度、山石与植物山石与地形等关系都十分困难，需要一定山石知识功底和造诣才能如愿。\n\n\n    　　据笔者所知，目前上海滩景观市场中能画山石施工图能指导施工的人寥寥无几，无法应对庞大的景观市场。\n\n\n\n    二、提高\n\n\n    　　绘制山石需要两方面的素质：一是心中装有“石”，二是手上会绘石。\n\n\n    　　借鉴：观察自然，对自然山水感受力要强，敏锐度高。通过观察，自然山的神韵、形体、走向、山石肌理等把它提炼出来成为设计者胸中的“丘壑”。一个胸中有“丘壑”的人才能画出山石来!若不能如愿，则去欣赏和临摹中国的山水画(这是中国的文化概念)。设计者把理解的山石的结构，千姿百态形状的石质变成具象的叠石作品，这也是造园师对自然山石的感觉，呈现在自己设计的景观之中。\n\n\n    　　表现：另一方面平时努力提高手绘能力，熟练的线条所表现出山石只有靠坚持不懈地画，没有捷径！同时提高辨别能力，分析他人成功作品和指出不足之处。提高自身赏石修养，先辨别作品的好与差，评论一番。最后自己动手设计。一个设计者连作品的好与坏都说不出所以然来，设计无从下手!\n\n\n    　　学习：从教课书上学习叠山置石的基本知识、学习如何相石、学习石的种类和叠石的章法等。掌握其理论层面上的应知应会；力争从实践施工项目中学习。工程开工后，有高级石匠师傅在现场堆石者，设计人员可直接向他们进行交流和学习。\n\n\n    三、过去\n\n\n    　　过去，五六十年代和七八十年代的园林景观设计师们没有电脑依赖，所有的图纸全靠手工画制，尤其传统园林“无石不园”。叠石用量大，凡是工地上要图纸必须保证准时送到工地!必须画出山石的平、立、剖面图，并计算出山石吨位重量!\n\n\n    　　那时，不会画问题也存在：学着画、练习画、模仿画，千方百计把胸中的山石丘壑“吐”出来，付诸施工......久而久之,孰能生巧。练就出一手画山石功夫。熟练画出的施工表现图给工人们施工，图纸一目了然，清晰明了，有效指导施工，又能给施工技师有发挥余地。\n\n\n    　　当时为了施工方便，一些年轻设计人员利用空余时间无偿地绘制了一些山石景观图，收集资料的同时练习手工技巧，一举多得。\n  ")])])}]
